/* src/App.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
}

.app {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  transition: 0.4s ease-out;
}

/* Arka planın üzerine hafif siyah bir perde çeker, yazılar okunsun diye */
.overlay {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3); 
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
}

/* --- Arama Kutusu --- */
.search-container {
  margin-bottom: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.search-bar {
  display: block;
  width: 100%;
  max-width: 400px;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 16px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-bar:focus {
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.3);
}

/* --- Popüler Şehirler (Üstteki küçük kartlar) --- */
.major-cities {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-bottom: 40px;
}

.mini-card {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 10px 20px;
  text-align: center;
  color: #fff;
  font-weight: bold;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px); /* Arkasını bulanıklaştırır */
  border: 1px solid rgba(255, 255, 255, 0.3);
  min-width: 100px;
  cursor: pointer;
  transition: 0.3s;
}

.mini-card:hover {
  background-color: rgba(255, 255, 255, 0.4);
  transform: scale(1.05);
}

.mini-card h3 {
  font-size: 16px;
  margin-bottom: 5px;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}

.mini-card p {
  font-size: 20px;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}

/* --- Ana Hava Durumu Kartı --- */
.weather-card {
  text-align: center;
  color: #FFF;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 30px 50px;
  box-shadow: 5px 10px 20px rgba(0,0,0,0.3);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  min-width: 300px;
}

.location {
  font-size: 32px;
  font-weight: 500;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.date {
  font-size: 18px;
  font-weight: 300;
  font-style: italic;
  margin-bottom: 20px;
  text-shadow: 1px 1px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #FFF;
  font-size: 80px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.description {
  color: #FFF;
  font-size: 32px;
  font-weight: 700;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
  text-transform: capitalize; /* Baş harfi büyütür */
}